<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process128"></a>

<p><br></p>
<p><span class="rvts17">转角遇到危险（</span><span class="rvts50">Most Dangerous When Cornered</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span></p>
<p><span class="rvts41">任务坐标：</span>
<br><span class="rvts20">X:424,Y:243</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：塔林（</span><span class="rvts20">Tarlene</span><span class="rvts21">）</span>
<br><span class="rvts20">X:466,Y:253</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：加雷斯（</span><span class="rvts20">Gareth</span><span class="rvts21">）</span>
<br><span class="rvts39"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">帮助加雷斯拜托困境</span>
<br><span class="rvts39"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1045.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts21">该任务可以在废墟兵营西边的草地上，找到塔林接到。注意，塔林东边是一个嚎叫者，尽量离它远一些，避免造成瞬间灭团。</span>
<br><span class="rvts39"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1046.jpg" display="block" max-width="100%"><span class="rvts39"></span></p>
<p class="rvps2"><span class="rvts39"></span>
<br><span class="rvts21">或者，可以直接在废墟中，看到加雷斯遭遇伏击。尽可能的拯救它。如果他活了下来，会在阿玛蒂亚避难所再次遇到他。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



